@media (max-width: 780px) {

    body.mobile-menu-opened{
        overflow: hidden;
    }

    .mobile_menu_bg {
        opacity: 0;
        position: fixed;
        z-index: 500;
        pointer-events: none;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background-color: #131313;
        transition: opacity 0.2s;

        body.mobile-menu-opened & {
            pointer-events: auto;
            opacity: 0.6;
        }
    }

    .header {
        position: static;

        .contact_info_wrapper {

            .contact_info {
                display: none;
            }
        }

        .header_logo {

            .header_logo_wrapper {
                padding: 20px 0;
                margin-left: 20px;
                justify-content: left;

                img {
                    width: 30px;
                    height: auto;
                }

                a {
                    font-size: 30px;
                }
            }
        }
    }

    .main_menu_wrapper {
        position: fixed;
        top: 0;
        right: 0;
        z-index: 1000;
        height: 100vh;

        .main_menu {
            background-color: #ffffff;
            opacity: 0;
            pointer-events: none;
            transition: opacity 0.2s;
            position: static;
            top: 0;
            height: 100%;


            menu {
                display: flex;
                flex-direction: column;
                justify-content: stretch;
                list-style: none;
                margin: 0;
                padding: 100px 0 0;
                position: static;

                &:before {
                    display: none;
                }

                &:after {
                    display: none;
                }

                > li {
                    position: static;

                    &:before {
                        display: none;
                    }

                    &.with_dropdown {

                        .dropdown {
                            list-style: none;
                            margin: 0;
                            display: block;
                            position: static;
                            opacity: 1;
                            pointer-events: auto;
                            padding-left: 40px;
                            a {
                                font-size: 16px;
                                text-transform: unset;
                                text-align: left;
                                padding: 6px;

                            }
                        }

                    }


                    &:nth-child(2):after {
                        display: block;
                        content: "";
                        width: 8px;
                        height: 4px;
                        background-image: url("img/arrow.png");
                        background-repeat: no-repeat;
                        background-position: center center;
                        position: absolute;
                        top: 162px;
                        right: 27px;
                    }

                    a {
                        display: block;
                        color: #383838;
                        font-family: "Plumb Condensed", sans-serif;
                        font-size: 18px;
                        font-weight: 400;
                        font-style: normal;
                        letter-spacing: normal;
                        text-align: left;
                        text-transform: uppercase;
                        text-decoration: none;
                        padding: 10px;
                        margin: 0;

                        span {
                            display: inline-block;
                            border-bottom: 2px solid transparent;
                            padding: 0 30px 2px;
                        }

                        &:hover {
                            color: #f5135c;
                        }
                    }


                    &.active a span {
                        border-color: transparent;
                    }
                }
            }

            body.mobile-menu-opened &{
                opacity: 1;
                pointer-events: auto;
            }
        }

        .mobile_menu_btn {
            display: flex;
            justify-content: center;
            flex-direction: column;
            align-items: center;
            position: fixed;
            top: 15px;
            right: 15px;
            background-color: #f5135c;
            width: 50px;
            height: 50px;
            border-radius: 50%;

            span {
                display: block;
                width: 25px;
                height: 2px;
                background-color: #ffffff;
                margin: 2px 0;
                transition: transform 0.2s, opacity 0.2s;
            }

            body.mobile-menu-opened & {
                span {
                    &:nth-child(1) {
                        transform: rotate(45deg) translate(4.25px, 4.25px);

                    }

                    &:nth-child(2) {
                        opacity: 0;
                    }

                    &:nth-child(3) {
                        transform: rotate(-45deg) translate(4.25px, -4.25px) ;
                    }
                }
            }
        }
    }

